// =============================================================================
// REQUEST VALIDATION MIDDLEWARE
// =============================================================================
// Helper functions for validating requests with Zod schemas

import { NextRequest } from 'next/server';
import { z } from 'zod';
import { createLogger } from './logger';

const logger = createLogger({ component: 'RequestValidator' });

export interface ValidationError {
  field: string;
  message: string;
  code: string;
}

export type ValidationResult<T> =
  | {
      valid: true;
      data: T;
    }
  | {
      valid: false;
      errors: ValidationError[];
      statusCode: number;
    };

/**
 * Validate request body against a Zod schema
 */
export async function validateRequestBody<T>(
  request: NextRequest,
  schema: z.ZodSchema<T>
): Promise<ValidationResult<T>> {
  try {
    const body = await request.json();
    const data = schema.parse(body);
    return { valid: true, data };
  } catch (error) {
    if (error instanceof z.ZodError) {
      const errors: ValidationError[] = error.errors.map((err) => ({
        field: err.path.join('.'),
        message: err.message,
        code: err.code,
      }));

      logger.warn({ errors, path: request.nextUrl.pathname }, 'Request validation failed');

      return {
        valid: false,
        errors,
        statusCode: 422, // Unprocessable Entity
      };
    }

    // JSON parse error or other error
    logger.error({ error }, 'Failed to parse request body');
    return {
      valid: false,
      errors: [{
        field: 'body',
        message: 'Invalid JSON in request body',
        code: 'invalid_json',
      }],
      statusCode: 400,
    };
  }
}

/**
 * Validate URL parameter against a Zod schema
 */
export function validateUrlParam<T>(
  param: string | string[] | undefined,
  schema: z.ZodSchema<T>
): ValidationResult<T> {
  const paramValue = Array.isArray(param) ? param[0] : param;

  if (!paramValue) {
    return {
      valid: false,
      errors: [{
        field: 'param',
        message: 'Required parameter is missing',
        code: 'missing_param',
      }],
      statusCode: 400,
    };
  }

  try {
    const data = schema.parse(paramValue);
    return { valid: true, data };
  } catch (error) {
    if (error instanceof z.ZodError) {
      const errors: ValidationError[] = error.errors.map((err) => ({
        field: err.path.join('.'),
        message: err.message,
        code: err.code,
      }));

      return {
        valid: false,
        errors,
        statusCode: 400,
      };
    }

    return {
      valid: false,
      errors: [{
        field: 'param',
        message: 'Invalid parameter format',
        code: 'invalid_param',
      }],
      statusCode: 400,
    };
  }
}

/**
 * Validate query parameters against a Zod schema
 */
export function validateQueryParams<T>(
  request: NextRequest,
  schema: z.ZodSchema<T>
): ValidationResult<T> {
  try {
    const params = Object.fromEntries(request.nextUrl.searchParams.entries());
    const data = schema.parse(params);
    return { valid: true, data };
  } catch (error) {
    if (error instanceof z.ZodError) {
      const errors: ValidationError[] = error.errors.map((err) => ({
        field: err.path.join('.'),
        message: err.message,
        code: err.code,
      }));

      return {
        valid: false,
        errors,
        statusCode: 400,
      };
    }

    return {
      valid: false,
      errors: [{
        field: 'query',
        message: 'Invalid query parameters',
        code: 'invalid_query',
      }],
      statusCode: 400,
    };
  }
}

/**
 * Create a standardized error response for validation failures
 */
export function createValidationErrorResponse(result: Extract<ValidationResult<any>, { valid: false }>) {
  return Response.json(
    {
      error: 'Validation failed',
      code: 'VALIDATION_ERROR',
      details: result.errors,
    },
    { status: result.statusCode }
  );
}

