// =============================================================================
// API KEY SCHEMAS
// =============================================================================
// Zod schemas for API key operations

import { z } from 'zod';

/**
 * Schema for creating a new API key
 */
export const createApiKeySchema = z.object({
  name: z.string()
    .min(1, 'Name is required')
    .max(100, 'Name must be less than 100 characters')
    .regex(/^[a-zA-Z0-9\s_-]+$/, 'Name can only contain letters, numbers, spaces, hyphens, and underscores'),
  
  role: z.enum(['admin', 'user', 'read-only'], {
    errorMap: () => ({ message: 'Role must be admin, user, or read-only' }),
  }),
  
  rateLimit: z.number()
    .int('Rate limit must be an integer')
    .min(1, 'Rate limit must be at least 1')
    .max(1000000, 'Rate limit must be less than 1,000,000')
    .optional(),
  
  expiresAt: z.string()
    .datetime('Must be a valid ISO datetime string')
    .optional(),
  
  metadata: z.record(z.any()).optional(),
}).strict();

/**
 * Schema for API key ID parameter
 */
export const apiKeyIdSchema = z.string()
  .regex(/^ak_[a-z0-9]+$/, 'Invalid API key ID format')
  .max(100, 'API key ID too long');

/**
 * Type exports
 */
export type CreateApiKeyInput = z.infer<typeof createApiKeySchema>;
export type ApiKeyId = z.infer<typeof apiKeyIdSchema>;

