// =============================================================================
// UNIFIED RATE LIMITING
// =============================================================================
// Single implementation for both Edge and Node.js environments.
// Supports distributed rate limiting via Redis with in-memory fallback.

import { NextRequest, NextResponse } from 'next/server';
import { getRateLimitStore, getEdgeRateLimitStore, type RateLimitStore } from './rate-limit-store';

// =============================================================================
// TYPES
// =============================================================================

export interface RateLimitConfig {
  windowMs: number;
  maxRequests: number;
  identifier: string;
}

export interface RateLimitResult {
  allowed: boolean;
  remaining: number;
  resetAt: number;
  limit: number;
}

export interface RateLimitTier {
  name: string;
  windowMs: number;
  maxRequests: number;
}

// =============================================================================
// DEFAULT TIERS
// =============================================================================

export const RATE_LIMIT_TIERS: Record<string, RateLimitTier> = {
  // Per-IP global limits
  global: { name: 'global', windowMs: 60 * 60 * 1000, maxRequests: 1000 },
  
  // Per-endpoint limits (per IP, per minute)
  endpoint: { name: 'endpoint', windowMs: 60 * 1000, maxRequests: 100 },
  
  // API key tiers (per hour)
  free: { name: 'free', windowMs: 60 * 60 * 1000, maxRequests: 100 },
  starter: { name: 'starter', windowMs: 60 * 60 * 1000, maxRequests: 1000 },
  pro: { name: 'pro', windowMs: 60 * 60 * 1000, maxRequests: 5000 },
  enterprise: { name: 'enterprise', windowMs: 60 * 60 * 1000, maxRequests: 50000 },
  
  // Cron job rate limiting (per minute)
  cron: { name: 'cron', windowMs: 60 * 1000, maxRequests: 1 },
};

// =============================================================================
// METRICS TRACKING
// =============================================================================

interface RateLimitMetrics {
  requests: Map<string, number>;      // tier:type -> total requests
  blocked: Map<string, number>;       // tier:type -> blocked requests
  latencies: Map<string, number[]>;   // tier:type -> latencies (last 100)
  lastReset: number;
}

const metrics: RateLimitMetrics = {
  requests: new Map(),
  blocked: new Map(),
  latencies: new Map(),
  lastReset: Date.now(),
};

const MAX_LATENCY_SAMPLES = 100;
const METRICS_RESET_INTERVAL_MS = 60 * 60 * 1000; // 1 hour

/**
 * Record a rate limit check
 */
function recordMetric(
  type: string,
  allowed: boolean,
  latencyMs: number
): void {
  // Reset metrics hourly
  const now = Date.now();
  if (now - metrics.lastReset > METRICS_RESET_INTERVAL_MS) {
    metrics.requests.clear();
    metrics.blocked.clear();
    metrics.latencies.clear();
    metrics.lastReset = now;
  }
  
  // Record request
  const key = type;
  metrics.requests.set(key, (metrics.requests.get(key) || 0) + 1);
  
  // Record blocked
  if (!allowed) {
    metrics.blocked.set(key, (metrics.blocked.get(key) || 0) + 1);
  }
  
  // Record latency (keep last N samples)
  let latencies = metrics.latencies.get(key);
  if (!latencies) {
    latencies = [];
    metrics.latencies.set(key, latencies);
  }
  latencies.push(latencyMs);
  if (latencies.length > MAX_LATENCY_SAMPLES) {
    latencies.shift();
  }
}

/**
 * Calculate percentile from sorted array
 */
function percentile(sorted: number[], p: number): number {
  if (sorted.length === 0) return 0;
  const idx = Math.ceil(sorted.length * p) - 1;
  return sorted[Math.max(0, Math.min(idx, sorted.length - 1))];
}

/**
 * Get rate limit metrics for observability
 */
export function getRateLimitMetrics(): {
  byType: Record<string, {
    total: number;
    blocked: number;
    blockRate: string;
    latency: { p50: number; p95: number; p99: number };
  }>;
  totals: {
    requests: number;
    blocked: number;
    blockRate: string;
  };
  lastReset: string;
} {
  const byType: Record<string, any> = {};
  let totalRequests = 0;
  let totalBlocked = 0;
  
  const types = new Set([
    ...metrics.requests.keys(),
    ...metrics.blocked.keys(),
  ]);
  
  for (const type of types) {
    const requests = metrics.requests.get(type) || 0;
    const blocked = metrics.blocked.get(type) || 0;
    const latencies = metrics.latencies.get(type) || [];
    
    totalRequests += requests;
    totalBlocked += blocked;
    
    // Calculate latency percentiles
    const sorted = [...latencies].sort((a, b) => a - b);
    
    byType[type] = {
      total: requests,
      blocked,
      blockRate: requests > 0 ? ((blocked / requests) * 100).toFixed(2) + '%' : '0%',
      latency: {
        p50: Math.round(percentile(sorted, 0.5)),
        p95: Math.round(percentile(sorted, 0.95)),
        p99: Math.round(percentile(sorted, 0.99)),
      },
    };
  }
  
  return {
    byType,
    totals: {
      requests: totalRequests,
      blocked: totalBlocked,
      blockRate: totalRequests > 0 
        ? ((totalBlocked / totalRequests) * 100).toFixed(2) + '%' 
        : '0%',
    },
    lastReset: new Date(metrics.lastReset).toISOString(),
  };
}

/**
 * Reset rate limit metrics (for testing)
 */
export function resetRateLimitMetrics(): void {
  metrics.requests.clear();
  metrics.blocked.clear();
  metrics.latencies.clear();
  metrics.lastReset = Date.now();
}

// =============================================================================
// HELPERS
// =============================================================================

/**
 * Extract API key from Authorization header
 */
export function extractApiKey(request: NextRequest): string | null {
  const authHeader = request.headers.get('authorization');
  if (!authHeader) return null;
  
  if (authHeader.startsWith('Bearer ')) {
    return authHeader.substring(7).trim();
  }
  return authHeader.trim();
}

/**
 * Get client IP address
 */
export function getClientIp(request: NextRequest): string {
  return (
    request.ip ||
    request.headers.get('x-forwarded-for')?.split(',')[0]?.trim() ||
    request.headers.get('x-real-ip') ||
    'unknown'
  );
}

/**
 * Hash a string for use as rate limit key (don't store full API keys)
 */
function hashKey(key: string): string {
  // Use first 16 chars as identifier (safe for rate limiting)
  return key.substring(0, 16);
}

// =============================================================================
// CORE RATE LIMIT CHECK
// =============================================================================

/**
 * Check rate limit against a store
 */
async function checkRateLimitAsync(
  store: RateLimitStore,
  config: RateLimitConfig,
  metricType?: string
): Promise<RateLimitResult> {
  const start = Date.now();
  const entry = await store.increment(config.identifier, config.windowMs);
  
  const allowed = entry.count <= config.maxRequests;
  const remaining = Math.max(0, config.maxRequests - entry.count);
  
  // Record metrics if type provided
  if (metricType) {
    recordMetric(metricType, allowed, Date.now() - start);
  }
  
  return {
    allowed,
    remaining,
    resetAt: entry.resetAt,
    limit: config.maxRequests,
  };
}

/**
 * Synchronous check for Edge runtime
 */
function checkRateLimitSync(
  store: RateLimitStore,
  config: RateLimitConfig
): RateLimitResult {
  const now = Date.now();
  const key = config.identifier;
  
  // Note: This is a simplified sync version that may not be perfectly accurate
  // but works for Edge runtime where we can't await
  const resetAt = now + config.windowMs;
  
  return {
    allowed: true, // Edge allows through, Node enforces
    remaining: config.maxRequests,
    resetAt,
    limit: config.maxRequests,
  };
}

// =============================================================================
// PUBLIC API - ASYNC (Node.js)
// =============================================================================

/**
 * Rate limit by API key (Node.js - database lookup)
 */
export async function rateLimitByApiKey(
  request: NextRequest,
  apiKeyRateLimit?: number
): Promise<RateLimitResult | null> {
  const token = extractApiKey(request);
  if (!token) return null;
  
  const store = await getRateLimitStore();
  const limit = apiKeyRateLimit || RATE_LIMIT_TIERS.starter.maxRequests;
  
  return checkRateLimitAsync(store, {
    windowMs: RATE_LIMIT_TIERS.starter.windowMs,
    maxRequests: limit,
    identifier: `api_key:${hashKey(token)}`,
  }, 'api_key');
}

/**
 * Rate limit by endpoint (Node.js)
 */
export async function rateLimitByEndpoint(
  request: NextRequest,
  endpointId: string,
  maxRequests?: number
): Promise<RateLimitResult> {
  const store = await getRateLimitStore();
  const ip = getClientIp(request);
  
  return checkRateLimitAsync(store, {
    windowMs: RATE_LIMIT_TIERS.endpoint.windowMs,
    maxRequests: maxRequests || RATE_LIMIT_TIERS.endpoint.maxRequests,
    identifier: `endpoint:${endpointId}:${ip}`,
  }, `endpoint:${endpointId}`);
}

/**
 * Rate limit by IP (Node.js)
 */
export async function rateLimitByIp(
  request: NextRequest,
  maxRequests?: number
): Promise<RateLimitResult> {
  const store = await getRateLimitStore();
  const ip = getClientIp(request);
  
  return checkRateLimitAsync(store, {
    windowMs: RATE_LIMIT_TIERS.global.windowMs,
    maxRequests: maxRequests || RATE_LIMIT_TIERS.global.maxRequests,
    identifier: `global:${ip}`,
  }, 'global');
}

/**
 * Rate limit cron job (prevent concurrent runs)
 */
export async function rateLimitCronJob(
  cronId: string,
  intervalMs?: number
): Promise<RateLimitResult> {
  const store = await getRateLimitStore();
  
  return checkRateLimitAsync(store, {
    windowMs: intervalMs || RATE_LIMIT_TIERS.cron.windowMs,
    maxRequests: RATE_LIMIT_TIERS.cron.maxRequests,
    identifier: `cron:${cronId}`,
  }, `cron:${cronId}`);
}

// =============================================================================
// PUBLIC API - SYNC (Edge Runtime)
// =============================================================================

/**
 * Rate limit by IP (Edge - sync, in-memory only)
 */
export function rateLimitByIpEdge(
  request: NextRequest,
  maxRequests?: number
): RateLimitResult {
  const store = getEdgeRateLimitStore();
  const ip = getClientIp(request);
  
  return checkRateLimitSync(store, {
    windowMs: RATE_LIMIT_TIERS.global.windowMs,
    maxRequests: maxRequests || RATE_LIMIT_TIERS.global.maxRequests,
    identifier: `global:${ip}`,
  });
}

/**
 * Rate limit by API key (Edge - sync, default limit only)
 */
export function rateLimitByApiKeyEdge(
  request: NextRequest,
  defaultLimit?: number
): RateLimitResult | null {
  const token = extractApiKey(request);
  if (!token) return null;
  
  const store = getEdgeRateLimitStore();
  
  return checkRateLimitSync(store, {
    windowMs: RATE_LIMIT_TIERS.starter.windowMs,
    maxRequests: defaultLimit || RATE_LIMIT_TIERS.starter.maxRequests,
    identifier: `api_key:${hashKey(token)}`,
  });
}

// =============================================================================
// RESPONSE HELPERS
// =============================================================================

/**
 * Add rate limit headers to response
 */
export function addRateLimitHeaders(
  response: NextResponse,
  result: RateLimitResult
): NextResponse {
  response.headers.set('X-RateLimit-Limit', result.limit.toString());
  response.headers.set('X-RateLimit-Remaining', result.remaining.toString());
  response.headers.set('X-RateLimit-Reset', new Date(result.resetAt).toISOString());
  return response;
}

/**
 * Create rate limit error response
 */
export function createRateLimitResponse(result: RateLimitResult): NextResponse {
  const resetDate = new Date(result.resetAt).toISOString();
  const retryAfter = Math.ceil((result.resetAt - Date.now()) / 1000);
  
  const response = NextResponse.json(
    {
      error: 'Rate limit exceeded',
      code: 'RATE_LIMIT_EXCEEDED',
      message: `Too many requests. Limit: ${result.limit} per hour. Try again after ${resetDate}`,
      retryAfter,
    },
    { status: 429 }
  );
  
  response.headers.set('Retry-After', retryAfter.toString());
  return addRateLimitHeaders(response, result);
}

// =============================================================================
// COMPOSITE CHECKS
// =============================================================================

/**
 * Check all rate limits (API key + endpoint + global)
 * Returns first exceeded limit, or null if all pass
 */
export async function checkAllRateLimits(
  request: NextRequest,
  options?: {
    endpointId?: string;
    apiKeyRateLimit?: number;
  }
): Promise<{ result: RateLimitResult; type: string } | null> {
  // 1. Global IP limit
  const globalResult = await rateLimitByIp(request);
  if (!globalResult.allowed) {
    return { result: globalResult, type: 'global' };
  }
  
  // 2. API key limit (if authenticated)
  const apiKeyResult = await rateLimitByApiKey(request, options?.apiKeyRateLimit);
  if (apiKeyResult && !apiKeyResult.allowed) {
    return { result: apiKeyResult, type: 'api_key' };
  }
  
  // 3. Endpoint limit (if specified)
  if (options?.endpointId) {
    const endpointResult = await rateLimitByEndpoint(request, options.endpointId);
    if (!endpointResult.allowed) {
      return { result: endpointResult, type: 'endpoint' };
    }
  }
  
  return null;
}

/**
 * Get rate limit info for response headers (async version)
 * Returns the most restrictive limit for informational headers
 */
export async function getRateLimitHeaders(
  request: NextRequest,
  endpointId?: string
): Promise<RateLimitResult | null> {
  const results: RateLimitResult[] = [];

  // Global IP limit
  const globalResult = await rateLimitByIp(request);
  results.push(globalResult);

  // API key limit
  const apiKeyResult = await rateLimitByApiKey(request);
  if (apiKeyResult) {
    results.push(apiKeyResult);
  }

  // Endpoint limit
  if (endpointId) {
    const endpointResult = await rateLimitByEndpoint(request, endpointId);
    results.push(endpointResult);
  }

  // Return the most restrictive (lowest remaining)
  if (results.length === 0) {
    return null;
  }

  return results.reduce((min, current) =>
    current.remaining < min.remaining ? current : min
  );
}

// =============================================================================
// EDGE-COMPATIBLE FUNCTIONS (Sync)
// =============================================================================

/**
 * Rate limit by endpoint (Edge - sync, in-memory only)
 */
export function rateLimitByEndpointEdge(
  request: NextRequest,
  endpointId: string,
  maxRequests?: number
): RateLimitResult {
  const store = getEdgeRateLimitStore();
  const ip = getClientIp(request);
  
  return checkRateLimitSync(store, {
    windowMs: RATE_LIMIT_TIERS.endpoint.windowMs,
    maxRequests: maxRequests || RATE_LIMIT_TIERS.endpoint.maxRequests,
    identifier: `endpoint:${endpointId}:${ip}`,
  });
}

/**
 * Check all rate limits (Edge-compatible, sync version)
 * Returns first exceeded limit, or null if all pass
 */
export function checkAllRateLimitsEdge(
  request: NextRequest,
  endpointId?: string
): { result: RateLimitResult; type: string } | null {
  // 1. Global IP limit
  const globalResult = rateLimitByIpEdge(request);
  if (!globalResult.allowed) {
    return { result: globalResult, type: 'global' };
  }
  
  // 2. API key limit (Edge uses default limit)
  const apiKeyResult = rateLimitByApiKeyEdge(request);
  if (apiKeyResult && !apiKeyResult.allowed) {
    return { result: apiKeyResult, type: 'api_key' };
  }
  
  // 3. Endpoint limit (if specified)
  if (endpointId) {
    const endpointResult = rateLimitByEndpointEdge(request, endpointId);
    if (!endpointResult.allowed) {
      return { result: endpointResult, type: 'endpoint' };
    }
  }
  
  return null;
}

/**
 * Get rate limit info for response headers (Edge-compatible, sync version)
 */
export function getRateLimitHeadersEdge(
  request: NextRequest,
  endpointId?: string
): RateLimitResult | null {
  const results: RateLimitResult[] = [];

  // Global IP limit
  results.push(rateLimitByIpEdge(request));

  // API key limit
  const apiKeyResult = rateLimitByApiKeyEdge(request);
  if (apiKeyResult) {
    results.push(apiKeyResult);
  }

  // Endpoint limit
  if (endpointId) {
    results.push(rateLimitByEndpointEdge(request, endpointId));
  }

  // Return the most restrictive (lowest remaining)
  if (results.length === 0) {
    return null;
  }

  return results.reduce((min, current) =>
    current.remaining < min.remaining ? current : min
  );
}

