// =============================================================================
// PAYMENT SCHEMAS
// =============================================================================
// Zod schemas for payment verification

import { z } from 'zod';

/**
 * Schema for payment header
 * Format: "x402 <base64_encoded_payment>"
 */
export const paymentHeaderSchema = z.string()
  .min(10, 'Payment header too short')
  .max(10000, 'Payment header too long')
  .refine(
    (val) => val.startsWith('x402 ') || val.startsWith('exact '),
    'Payment header must start with "x402 " or "exact "'
  )
  .refine(
    (val) => {
      const parts = val.split(' ');
      if (parts.length < 2) return false;
      // Check if second part is valid base64
      const base64Part = parts.slice(1).join(' ');
      try {
        atob(base64Part);
        return true;
      } catch {
        return false;
      }
    },
    'Payment header must contain valid base64-encoded payment data'
  );

/**
 * Schema for payment verification request body
 */
export const paymentVerificationSchema = z.object({
  paymentHeader: paymentHeaderSchema,
  
  endpointId: z.string()
    .regex(/^ep_\d+_[a-z0-9]+$/, 'Invalid endpoint ID format')
    .optional(),
  
  metadata: z.record(z.any()).optional(),
}).strict();

/**
 * Type exports
 */
export type PaymentHeader = z.infer<typeof paymentHeaderSchema>;
export type PaymentVerificationInput = z.infer<typeof paymentVerificationSchema>;

