// =============================================================================
// RATE LIMIT STORE
// =============================================================================
// Pluggable backing store for rate limiting with Upstash Redis support and in-memory fallback.
// Supports distributed rate limiting across serverless instances.
//
// Environment Variables:
// - UPSTASH_REDIS_REST_URL: Upstash Redis REST URL (from Vercel integration)
// - UPSTASH_REDIS_REST_TOKEN: Upstash Redis REST token (from Vercel integration)

import { createLogger } from './logger';

const logger = createLogger({ component: 'RateLimitStore' });

export interface RateLimitEntry {
  count: number;
  resetAt: number;
}

export interface RateLimitStore {
  get(key: string): Promise<RateLimitEntry | null>;
  set(key: string, entry: RateLimitEntry): Promise<void>;
  increment(key: string, windowMs: number): Promise<RateLimitEntry>;
}

// =============================================================================
// IN-MEMORY STORE (fallback)
// =============================================================================

class InMemoryStore implements RateLimitStore {
  private store = new Map<string, RateLimitEntry>();
  private cleanupInterval: ReturnType<typeof setInterval> | null = null;

  constructor() {
    // Cleanup every 5 minutes
    if (typeof setInterval !== 'undefined') {
      this.cleanupInterval = setInterval(() => this.cleanup(), 5 * 60 * 1000);
    }
  }

  private cleanup(): void {
    const now = Date.now();
    const keysToDelete: string[] = [];
    this.store.forEach((value, key) => {
      if (value.resetAt < now) {
        keysToDelete.push(key);
      }
    });
    keysToDelete.forEach(key => this.store.delete(key));
  }

  async get(key: string): Promise<RateLimitEntry | null> {
    const entry = this.store.get(key);
    if (!entry || entry.resetAt < Date.now()) {
      return null;
    }
    return entry;
  }

  async set(key: string, entry: RateLimitEntry): Promise<void> {
    this.store.set(key, entry);
  }

  async increment(key: string, windowMs: number): Promise<RateLimitEntry> {
    const now = Date.now();
    const existing = this.store.get(key);

    if (!existing || existing.resetAt < now) {
      const entry = { count: 1, resetAt: now + windowMs };
      this.store.set(key, entry);
      return entry;
    }

    existing.count++;
    this.store.set(key, existing);
    return existing;
  }
}

// =============================================================================
// UPSTASH REDIS STORE (distributed)
// =============================================================================

class UpstashRedisStore implements RateLimitStore {
  private redis: any;

  constructor(redisClient: any) {
    this.redis = redisClient;
  }

  async get(key: string): Promise<RateLimitEntry | null> {
    try {
      const data = await this.redis.get(`ratelimit:${key}`);
      if (!data) return null;
      
      // Upstash returns parsed JSON automatically if it's a JSON string
      const entry = typeof data === 'string' ? JSON.parse(data) : data;
      if (entry.resetAt < Date.now()) return null;
      return entry as RateLimitEntry;
    } catch (error) {
      logger.error({ error, key }, 'Upstash Redis GET failed');
      return null;
    }
  }

  async set(key: string, entry: RateLimitEntry): Promise<void> {
    try {
      const ttlMs = Math.max(entry.resetAt - Date.now(), 1000);
      const ttlSeconds = Math.ceil(ttlMs / 1000);
      
      await this.redis.set(
        `ratelimit:${key}`,
        JSON.stringify(entry),
        { ex: ttlSeconds }
      );
    } catch (error) {
      logger.error({ error, key }, 'Upstash Redis SET failed');
    }
  }

  async increment(key: string, windowMs: number): Promise<RateLimitEntry> {
    try {
      const redisKey = `ratelimit:${key}`;
      const now = Date.now();

      // Get current entry
      const data = await this.redis.get(redisKey);
      
      let count = 1;
      let resetAt = now + windowMs;
      
      if (data) {
        const entry = typeof data === 'string' ? JSON.parse(data) : data;
        if (entry.resetAt > now) {
          count = entry.count + 1;
          resetAt = entry.resetAt;
        }
      }
      
      const newEntry = { count, resetAt };
      const ttlMs = resetAt - now;
      const ttlSeconds = Math.ceil(ttlMs / 1000);
      
      await this.redis.set(redisKey, JSON.stringify(newEntry), { ex: ttlSeconds });
      
      return newEntry;
    } catch (error) {
      logger.error({ error, key }, 'Upstash Redis INCR failed');
      // Fallback: return a permissive entry
      return { count: 1, resetAt: Date.now() + windowMs };
    }
  }
}

// =============================================================================
// STORE FACTORY
// =============================================================================

let storeInstance: RateLimitStore | null = null;

/**
 * Get the rate limit store singleton.
 * Uses Upstash Redis if configured, otherwise falls back to in-memory.
 */
export async function getRateLimitStore(): Promise<RateLimitStore> {
  if (storeInstance) {
    return storeInstance;
  }

  // Check for Upstash Redis environment variables (set by Vercel integration)
  const upstashUrl = process.env.UPSTASH_REDIS_REST_URL;
  const upstashToken = process.env.UPSTASH_REDIS_REST_TOKEN;

  if (upstashUrl && upstashToken) {
    try {
      // Dynamic import to avoid build issues
      const { Redis } = await import('@upstash/redis');
      const redis = new Redis({
        url: upstashUrl,
        token: upstashToken,
      });

      // Test connection
      await redis.ping();
      
      logger.info('Rate limit store using Upstash Redis');
      storeInstance = new UpstashRedisStore(redis);
      return storeInstance;
    } catch (error) {
      logger.warn({ error }, 'Upstash Redis connection failed, falling back to in-memory');
    }
  }

  // Legacy support: check for REDIS_URL (ioredis)
  const redisUrl = process.env.REDIS_URL;
  if (redisUrl) {
    try {
      const { Redis } = await import('ioredis');
      const redis = new Redis(redisUrl, {
        maxRetriesPerRequest: 3,
        enableReadyCheck: true,
        connectTimeout: 5000,
        lazyConnect: true,
      });

      await redis.connect();
      logger.info('Rate limit store using Redis (ioredis)');
      
      // Use a simple wrapper that matches UpstashRedisStore behavior
      storeInstance = {
        async get(key: string): Promise<RateLimitEntry | null> {
          const data = await redis.get(`ratelimit:${key}`);
          if (!data) return null;
          const entry = JSON.parse(data) as RateLimitEntry;
          if (entry.resetAt < Date.now()) return null;
          return entry;
        },
        async set(key: string, entry: RateLimitEntry): Promise<void> {
          const ttlMs = Math.max(entry.resetAt - Date.now(), 1000);
          await redis.set(`ratelimit:${key}`, JSON.stringify(entry), 'PX', ttlMs);
        },
        async increment(key: string, windowMs: number): Promise<RateLimitEntry> {
          const now = Date.now();
          const redisKey = `ratelimit:${key}`;
          const data = await redis.get(redisKey);
          
          let count = 1;
          let resetAt = now + windowMs;
          
          if (data) {
            const entry = JSON.parse(data);
            if (entry.resetAt > now) {
              count = entry.count + 1;
              resetAt = entry.resetAt;
            }
          }
          
          const newEntry = { count, resetAt };
          const ttlMs = resetAt - now;
          await redis.set(redisKey, JSON.stringify(newEntry), 'PX', ttlMs);
          return newEntry;
        }
      };
      return storeInstance;
    } catch (error) {
      logger.warn({ error }, 'Redis connection failed, falling back to in-memory');
    }
  }

  logger.info('Rate limit store using in-memory (non-distributed)');
  storeInstance = new InMemoryStore();
  return storeInstance;
}

/**
 * Synchronous in-memory store for Edge runtime.
 * Edge runtime can use Upstash but requires async initialization.
 */
export function getEdgeRateLimitStore(): RateLimitStore {
  // Edge runtime uses in-memory for sync access
  // For async Edge operations, use getRateLimitStore()
  if (!storeInstance) {
    storeInstance = new InMemoryStore();
  }
  return storeInstance;
}

/**
 * Reset the store singleton (for testing)
 */
export function resetRateLimitStore(): void {
  storeInstance = null;
}
